/* eslint-disable prettier/prettier */
/* eslint-disable react/prop-types */
import { CSmartPagination, CSmartTable } from '@coreui/react-pro'
import React, { useEffect, useState } from 'react'

const CustomTable = ({ record, columns, scopedColumns, cantPag, searchData, resertFilter }) => {
  const [page, setPage] = useState(1)
  const [itemPerPage, setitemPerPage] = useState(10)
  const [columnChange, setColumnChange] = useState(false)
  const [sortSearch, setSortSearch] = useState({ [columns[0].key]: '0' })
  const [sortTable, setSortTable] = useState({})
  const [filters, setFilters] = useState({})

  useEffect(() => {
    setFilters({})
    setSortTable({})
  }, [resertFilter])

  const changeItemPerPage = (i) => {
    setitemPerPage(i)
    setPage(1)
    const data = {
      page: 0,
      size: i,
      filter: filters,
      sort: sortSearch
    }
    searchData(data)
  }
  const changePage = (i) => {
    setPage(i === 0 ? i + 1 : i)
    const data = {
      page: i === 0 ? i : i - 1,
      size: itemPerPage,
      filter: filters,
      sort: sortSearch
    }
    searchData(data)
  }
  const changeSort = (sortData) => {
    if (Object.entries(sortData).length > 0) {
      setPage(1)
      setSortTable(sortData)
      setSortSearch({ [sortData.column]: `${sortData.state}` })
      const data = {
        page: 0,
        size: itemPerPage,
        filter: filters,
        sort: { [sortData.column]: `${sortData.state}` }
      }
      searchData(data)
    }
  }
  const changeFilter = (filterData) => {
    setPage(1)
    const data = {
      page: 0,
      size: itemPerPage,
      filter: filterData,
      sort: sortSearch
    }
    searchData(data)
  }
  return (
    <>
      <CSmartTable
        tableProps={{
          striped: true,
          hover: true,
        }}
        footer
        items={record}
        columns={columns}
        sorterValue={sortTable}
        columnSorter={{
          external: true,
          resetable: true
        }}
        columnFilterValue={filters}
        columnFilter={
          {
            lazy: true,
            external: true,
          }
        }
        itemsPerPageSelect
        itemsPerPage={itemPerPage}
        onSorterChange={(i) => {
          changeSort(i)
        }}
        onColumnFilterChange={(i) => {
          if (Object.entries(i).length > 0) {
            setFilters(i)
            clearTimeout(columnChange)
            setColumnChange(
              setTimeout(() => {
                changeFilter(i);
              }, 600),
            )
          }
        }}
        onItemsPerPageChange={(i) => {
          changeItemPerPage(i)
        }}
        scopedColumns={scopedColumns}
      />
      {cantPag > 0 ? (
        <CSmartPagination
          activePage={page}
          pages={cantPag}
          onActivePageChange={(i) => {
            changePage(i)
          }}
        />
      ) : ""
      }
    </>
  )
}
export default CustomTable
