import React, { useRef, useState } from 'react'
import upload_file from 'src/assets/images/upload_files.png'
import delete_file from 'src/assets/images/delete_icons.jpg'
import 'src/styles.css'
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CCollapse,
  CDatePicker,
  CForm,
  CFormInput,
  CFormLabel,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow,
  CToast,
  CToastBody,
  CToaster,
  CToastHeader,
} from '@coreui/react-pro'
import {
  serviceDeleteNcbf,
  serviceExistNcbf,
  serviceSearchNcbf,
  serviceUploadFile,
} from 'src/service/ncbf_file/ncbf'
import moment from 'moment'
import CustomTable from 'src/components/custom_table/CustomTable'
import CustomToast from 'src/components/custom_toast/CustomToast'
import CustomSearchInput from 'src/components/custom_search_input/CustomSearchInput'
import { useDispatch } from 'react-redux'
import { progressAction } from 'src/store/actions/progressActions'
import { columns } from './ncbf_table_utils'
import { exportToExcel } from 'src/reusable/ExcelExport'

const NCBFFile = () => {
  const dispatch = useDispatch()
  const [details, setDetails] = useState([])
  const [showModal, setShowModal] = useState(false)
  const [showModalExist, setShowModalExist] = useState(false)
  const [showModalDelete, setShowModalDelete] = useState(false)
  const [file, setFile] = useState([])
  const [dateFile, setDateFile] = useState(moment().format('MM/DD/YYYY'))
  const [toast, addToast] = useState(0)
  const [record, setRecord] = useState([])
  const [header, setHeader] = useState([])
  const [trailer, setTrailer] = useState([])
  const [cantPag, setCantPag] = useState(0)
  const [itemPerPage, setitemPerPage] = useState(10)
  const toaster = useRef()
  const [canSearch, setCanSearch] = useState(false)
  const [resetFilter, setResetFilter] = useState(false)
  const [totalElement, setTotalElement] = useState(0)
  const [searchData, setSearchData] = useState({})

  const scopedColumns = {
    show_details: (item) => {
      return (
        <td className="py-2">
          <CButton
            color="primary"
            variant="outline"
            shape="square"
            size="sm"
            onClick={() => {
              toggleDetails(item)
            }}
          >
            {details.includes(item) ? 'Hide' : 'Show'}
          </CButton>
        </td>
      )
    },
    details: (item) => {
      return (
        <CCollapse visible={details.includes(item)}>
          <CCardBody>
            <CRow>
              <CCol xs={4}>
                <p className="text-dark">Card Prefix Length: {item.card_prefix_length}</p>
                <p className="text-dark">Pan Minimum Length: {item.pan_minimum_length}</p>
                <p className="text-dark">Pan Maximinus Length: {item.pan_maximum_length}</p>
                <p className="text-dark">Bill Pay Indicator: {item.bill_pay_indicator}</p>
                <p className="text-dark">
                  Internet Ecommerce Indicator: {item.internet_ecommerce_indicator}
                </p>
              </CCol>
              <CCol xs={4}>
                <p className="text-dark">Card Type: {item.card_type}</p>
                <p className="text-dark">
                  ATM Priority Routing Indicator:
                  {item.atm_priority_routing_indicator}
                </p>
                <p className="text-dark">
                  POS Priority Routing Indicator:
                  {item.pos_priority_routing_indicator}
                </p>
                <p className="text-dark">
                  Interchange Fee Indicator: {item.interchange_fee_indicator}
                </p>
                <p className="text-dark">
                  Money Transfer Indicator: {item.money_transfer_indicator}
                </p>
                <p className="text-dark">Signature Indicator: {item.signature_indicator}</p>
              </CCol>
              <CCol xs={4}>
                <p className="text-dark">ATM indicator: {item.atm_indicator}</p>
                <p className="text-dark">POS Indicator: {item.pos_indicator}</p>
                <p className="text-dark">
                  Auto Substantiation Indicator:
                  {item.auto_substantiation_indicator}
                </p>
                <p className="text-dark">Token Indicator: {item.token_indicator}</p>
                <p className="text-dark">Pan Maximinus Length: {item.filler}</p>
                <p className="text-dark">BIN File Format ID: {item.bin_file_format_id}</p>
              </CCol>
              <CCol xs={12}>
                <p className="text-dark">BIN File Record Data: {item.bin_file_record_data}</p>
              </CCol>
            </CRow>
          </CCardBody>
        </CCollapse>
      )
    },
  }
  const toggleDetails = (item) => {
    if (details.includes(item)) {
      setDetails([])
    } else {
      setDetails([item])
    }
  }
  const uploadFile = async () => {
    var formData = new FormData()
    formData.append('file', file[0])
    setShowModal(false)
    let start = new Date().getTime()
    dispatch(
      progressAction({
        percentage: 0,
        isUpload: true,
        startTime: start,
        durationTime: 0,
        speed: 0,
      }),
    )
    const response = await serviceUploadFile(formData, dispatch, progressAction, start)
    if (response.status === '200') {
      setFile([])
      addToast(CustomToast({ msg: 'File uploaded successfully' }))
      setDateFile((state) => {
        state = moment().format('MM/DD/YYYY')
        return state
      })
      const data = {
        page: 0,
        size: itemPerPage,
        filter: {},
        sort: { sequence_number: '0' },
      }
      setCanSearch(true)
      loadData(data)
    }
  }
  const search = () => {
    const data = {
      page: 0,
      size: itemPerPage,
      filter: {},
      sort: { sequence_number: '0' },
    }
    setResetFilter(true)
    setCanSearch(true)
    loadData(data)
  }

  const changePageItemPerPage = (data) => {
    setitemPerPage(data.size)
    if (canSearch) {
      loadData(data)
    }
  }
  const loadData = async (data, date = null) => {
    let dataSearch = {
      upload_file_date: date !== null ? date : moment(dateFile).format('YYYY-MM-DD'),
      ...data,
    }
    setSearchData(dataSearch)
    const response = await serviceSearchNcbf(dataSearch)
    if (response.status === '200') {
      console.log(response.data)
      if (response.data) {
        const { records, header, trailer, total_elements, total_pages } = response.data
        setRecord(records)
        setHeader(header)
        setTrailer(trailer)
        setCantPag(total_pages)
        setTotalElement(total_elements)
      } else {
        setRecord([])
        setHeader([])
        setTrailer([])
        setCantPag(0)
        setTotalElement(0)
      }
      setResetFilter(false)
    }
  }
  const exitsFile = async (e) => {
    e.preventDefault()
    // let contentBuffer = await readFile(file[0])
    // let header = contentBuffer.split('\n')[0]
    setShowModal(false)
    const response = await serviceExistNcbf()
    if (response.data) {
      setShowModalExist(true)
    } else if (response.data !== null) {
      uploadFile()
    }
  }
  const readFile = async (file) => {
    return new Promise((resolve, reject) => {
      let reader = new FileReader()
      reader.onload = () => {
        resolve(reader.result)
      }
      reader.onerror = reject
      reader.readAsText(file)
    })
  }
  const overwrite = (e) => {
    e.preventDefault()
    setShowModalExist(false)
    uploadFile()
  }
  const deleteFile = async (e) => {
    e.preventDefault()
    setShowModalDelete(false)
    let date = moment(dateFile).format('YYYY-MM-DD')
    const response = await serviceDeleteNcbf({ date: `${date}` })
    if (response.status === '200') {
      setRecord([])
      setHeader([])
      setTrailer([])
      setCantPag(0)
    }
  }
  const exportData = async () => {
    let data = { ...searchData, size: totalElement }
    const response = await serviceSearchNcbf(data)
    if (response.status === '200') {
      console.log(response.data)
      if (response.data) {
        const { records } = response.data
        exportToExcel(records, 'NCBF')
      }
    }
  }
  return (
    <>
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardBody>
              <CRow>
                <CCol xs={12} className="mb-4">
                  <CRow>
                    <CCol xs={2}>
                      <CButton
                        component="input"
                        type="button"
                        color="primary"
                        value="Upload File"
                        onClick={() => {
                          setShowModal(true)
                        }}
                      />
                    </CCol>
                    <CCol xs={4}>
                      <CDatePicker
                        locale="en-US"
                        inputReadOnly
                        date={dateFile}
                        dayFormat={'2-digit'}
                        onDateChange={(e) => {
                          setDateFile(moment(e).format('YYYY-MM-DD'))
                        }}
                      />
                    </CCol>
                    <CCol xs={1}>
                      <CButton
                        component="input"
                        type="button"
                        color="info"
                        value="Search"
                        onClick={() => {
                          search()
                        }}
                      />
                    </CCol>
                    {record.length > 0 ? (
                      <>
                        <CCol xs={1}>
                          <CButton
                            component="input"
                            type="button"
                            color="danger"
                            value="Delete"
                            onClick={() => {
                              setShowModalDelete(true)
                            }}
                          />
                        </CCol>
                        <CCol xs={1}>
                          <CButton
                            component="input"
                            type="button"
                            color="primary"
                            value="Export"
                            onClick={() => {
                              exportData()
                            }}
                          />
                        </CCol>
                      </>
                    ) : (
                      ''
                    )}
                  </CRow>
                </CCol>
                <CCol xs={6}>
                  <h3>Header Info</h3>
                  <CRow>
                    <CCol xs={6}>
                      <p className="text-dark">Type: {header.type}</p>
                      <p className="text-dark">Sequence Number: {header.sequence_number}</p>
                      <p className="text-dark">Excat Date: {header.exact_date}</p>
                    </CCol>
                    <CCol xs={6}>
                      <p className="text-dark">Record Length: {header.record_length}</p>
                      <p className="text-dark">Reserved: {header.reserved}</p>
                    </CCol>
                    <CCol xs={12}>
                      <p className="text-dark">File Description: {header.file_description}</p>
                    </CCol>
                  </CRow>
                </CCol>
                <CCol xs={6}>
                  <h3>Trailer Info</h3>
                  <CRow>
                    <CCol xs={6}>
                      <p className="text-dark">Type: {trailer.type}</p>
                      <p className="text-dark">Sequence Number: {trailer.sequence_number}</p>
                      <p className="text-dark">Excat Date: {trailer.exact_date}</p>
                    </CCol>
                    <CCol xs={6}>
                      <p className="text-dark">
                        Detail Record Count: {trailer.detail_record_count}
                      </p>
                      <p className="text-dark">Reserved: {trailer.reserved}</p>
                    </CCol>
                  </CRow>
                </CCol>
                <CCol xs={12}>
                  <CustomTable
                    record={record}
                    columns={columns}
                    scopedColumns={scopedColumns}
                    cantPag={cantPag}
                    searchData={changePageItemPerPage}
                    resertFilter={resetFilter}
                  />
                </CCol>
              </CRow>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      <CModal className="modal_windows" visible={showModal}>
        <CForm className="windows" onSubmit={exitsFile}>
          <img
            style={{ width: '35px', marginLeft: '35px', marginTop: '30px' }}
            src={upload_file}
            alt=""
          />
          <CButton
            className="close_windows"
            style={{
              width: '10px',
              marginLeft: '370px',
              marginTop: '-20px',
              border: 'none',
            }}
            variant="ghost"
            onClick={() => setShowModal(false)}
          ></CButton>
          <CModalBody>
            <div style={{ marginTop: '10px' }}>
              <CFormLabel style={{ fontSize: '20px' }} htmlFor="formFile">
                Select the file you want to upload
              </CFormLabel>
              <CFormInput
                style={{ marginTop: '10px' }}
                type="file"
                placeholder="select a file"
                id="formFile"
                onChange={(e) => {
                  setFile(e.target.files)
                }}
              />
            </div>
            <div style={{ marginTop: '50px' }}>
              <CButton
                style={{ width: '150px', height: '45px', marginLeft: '63px' }}
                color="dark"
                variant="outline"
                onClick={() => setShowModal(false)}
              >
                Close
              </CButton>
              <CButton
                style={{ width: '150px', height: '45px', marginLeft: '40px' }}
                color="primary"
                type="submit"
                className="button"
              >
                Upload
              </CButton>
            </div>
          </CModalBody>
        </CForm>
      </CModal>

      <CModal className="modal_windows" visible={showModalExist}>
        <CForm className="windows" onSubmit={overwrite}>
          <img
            style={{ width: '35px', marginLeft: '43px', marginTop: '30px', position: 'absolute' }}
            src={delete_file}
            alt=""
          />
          <div className="icono_delete_1"></div>
          <div className="icono_delete_2"></div>
          <CButton
            className="close_windows"
            style={{
              marginLeft: '450px',
              marginTop: '-155px',
              border: 'none',
              position: 'relative',
            }}
            variant="ghost"
            onClick={() => setShowModalExist(false)}
          ></CButton>
          <div>
            <CFormLabel
              style={{ fontSize: '25px', marginTop: '2px', marginLeft: '40px', fontWeight: '550' }}
              htmlFor="formFile"
            >
              Delete record
            </CFormLabel>
            <br></br>
            <CFormLabel
              style={{ fontSize: '17px', marginLeft: '40px', marginRight: '33px' }}
              htmlFor="formFile"
            >
              Thereâ€™s already a file for this day. Do you want to overwrite it?
            </CFormLabel>
          </div>

          <div style={{ marginTop: '30px', marginBottom: '25px', marginLeft: '10px' }}>
            <CButton
              style={{
                width: '150px',
                height: '45px',
                marginLeft: '70px',
                fontSize: '17px',
                color: 'black',
              }}
              color="dark"
              variant="outline"
              onClick={() => setShowModalExist(false)}
            >
              Close
            </CButton>
            <CButton
              className="delete_button"
              style={{
                width: '150px',
                height: '45px',
                marginLeft: '40px',
                fontSize: '17px',
                fontWeight: '550',
              }}
              type="submit"
            >
              Overwrite
            </CButton>
          </div>
        </CForm>
      </CModal>

      <CModal className="modal_windows" visible={showModalDelete}>
        <CForm className="windows" onSubmit={deleteFile}>
          <img
            style={{ width: '35px', marginLeft: '43px', marginTop: '30px', position: 'absolute' }}
            src={delete_file}
            alt=""
          />
          <div className="icono_delete_1"></div>
          <div className="icono_delete_2"></div>
          <CButton
            className="close_windows"
            style={{
              marginLeft: '450px',
              marginTop: '-155px',
              border: 'none',
              position: 'relative',
            }}
            variant="ghost"
            onClick={() => setShowModalDelete(false)}
          ></CButton>
          <div>
            <CFormLabel
              style={{ fontSize: '25px', marginTop: '2px', marginLeft: '40px', fontWeight: '550' }}
              htmlFor="formFile"
            >
              Delete record
            </CFormLabel>
            <br></br>
            <CFormLabel
              style={{ fontSize: '17px', marginLeft: '40px', marginRight: '33px' }}
              htmlFor="formFile"
            >
              Do you want to delete this file?
            </CFormLabel>
          </div>

          <div style={{ marginTop: '30px', marginBottom: '25px', marginLeft: '10px' }}>
            <CButton
              style={{
                width: '150px',
                height: '45px',
                marginLeft: '70px',
                fontSize: '17px',
                color: 'black',
              }}
              color="dark"
              variant="outline"
              onClick={() => setShowModalDelete(false)}
            >
              Close
            </CButton>
            <CButton
              className="delete_button"
              style={{
                width: '150px',
                height: '45px',
                marginLeft: '40px',
                fontSize: '17px',
                fontWeight: '550',
              }}
              type="submit"
            >
              Delete
            </CButton>
          </div>
        </CForm>
      </CModal>
      <CToaster ref={toaster} push={toast} placement="top-end" />
    </>
  )
}

export default NCBFFile
