import apiInstance from '../ApiConfig'

export const serviceUploadFile = async (fields, dist, action, startTime) => {
  return apiInstance
    .post(`/ncbf/upload`, fields, {
      headers: {
        'Content-Type': 'multipart/form-data',
        'Keep-Alive': 'timeout=60000',
      },
      onUploadProgress: (progressEvent) => {
        let percentage = (progressEvent.progress * 100).toFixed(2)
        let end = new Date().getTime()
        let duration = (end - startTime) / 1000
        let bps = progressEvent.loaded / duration
        let kbps = (bps / 1024).toFixed(2)
        let time = (progressEvent.total - progressEvent.loaded) / bps
        dist(
          action({
            percentage: percentage,
            isUpload: progressEvent.progress < 1 ? true : false,
            startTime: startTime,
            durationTime: time,
            speed: kbps,
          }),
        )
      },
    })
    .then((response) => {
      return response.data
    })
    .catch((err) => {
      dist(action({ percentage: 0, isUpload: false }))
      return err.response.data
    })
}

